<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务结算</el-breadcrumb-item>
                <el-breadcrumb-item>财务费用结算</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增-->
            <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i>新增费用单</el-button>
            </div>
            <!--新增表单-->
            <el-dialog :title="addForm.costCode==''?'新增费用单':'修改费用单'" :visible.sync="dialogAddForm" class="add-form" :close-on-click-modal="false" size="small">
                <el-form :model="addForm" label-width="150px" ref="addForm">
                    <el-form-item label="商家编号" prop="supplierCode" :rules="{required:true,message:'该字段必填'}">
                        <el-input v-model="addForm.supplierCode" placeholder="费用单名称"></el-input>
                    </el-form-item>
                    <el-form-item label="公司名称" prop="companyName" >
                        <el-input v-model="addForm.companyName"  :disabled="true"></el-input>
                    </el-form-item>
                    <el-form-item label="费用金额(USD)" prop="costPriceUSD_c" :rules="[{required:true,message:'该字段必填'},{ type: 'number', message: '该字段必须为数字值'}]">
                        <el-input v-model.number="addForm.costPriceUSD_c" ></el-input>
                    </el-form-item>
                    <el-form-item label="结算日汇率" prop="exchangeRate" :rules="{ type: 'number', message: '该字段必须为数字值'}">
                        <el-input v-model.number="addForm.exchangeRate"  ></el-input>
                    </el-form-item>
                    <el-form-item label="费用金额(CNY)" prop="costPriceCNY_c" :rules="{ type: 'number', message: '该字段必须为数字值'}">
                        <el-input v-model.number="addForm.costPriceCNY_c"  ></el-input>
                    </el-form-item>
                    <el-form-item label="费用发生时间" prop="costTime" >
                        <el-date-picker type="date" placeholder="选择日期" v-model="addForm.costTime" :picker-options="pickerOptions"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="费用单状态" prop="costType">
                        <el-select v-model="addForm.costType" size="small" clearable>          
                            <el-option :value="item.val" :label="item.label" v-for="(item,index) in myConfig.status.costType" :key="item.val"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false">取 消</el-button>
                    <el-button type="primary" @click="submit_add">确 定</el-button>
                </div>
            </el-dialog>
        </div>
        
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="费用单号">
                            <el-input v-model="searchForm.costCode" placeholder="费用单号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="结算状态">
                            <el-select v-model="searchForm.status" placeholder="结算状态" size="small" clearable>          
                                <el-option value="1" label="待审核"></el-option>
                                <el-option value="3" label="待结算"></el-option>
                                <el-option value="4" label="已结算"></el-option>
                                <el-option value="6" label="审核不通过"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="结算号">
                            <el-input v-model="searchForm.settlementCode" placeholder="结算号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="公司名称">
                            <el-input v-model="searchForm.companyName" placeholder="公司名称" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="费用项目类型">
                            <el-select v-model="searchForm.costType" size="small" clearable>          
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.costType" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="费用发生始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.costTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="费用发生止">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.costTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <div class="import">
            <el-upload class="upload-import" action="/proxy/report/api/import/financial/cost" :on-success="doImport" :accept="'file/file.xls'" :file-list="fileList">
                <el-button size="small" type="primary">导入</el-button>
            </el-upload>
        </div>
        <!-- 批量操作 -->
        <div class="doAction">
            <el-button type="primary" @click="approval('',3)" size="small">批量审核</el-button>
            <el-button type="primary" @click="approval('',6)" size="small">批量审核不通过</el-button>
            <el-button type="primary" @click="costDelete('')" size="small">批量删除</el-button>
            
           
        </div>
        <!--列表-->
        <el-table :data="reportList"  style="width: 100%" v-loading="loading" class="report" max-height="520" border stripe highlight-current-row @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40" fixed="left"></el-table-column>
            <el-table-column label="费用单号" prop="costCode" align="center" width="180"></el-table-column>
            <el-table-column label="商家编号" prop="supplierCode" align="center" width="160"></el-table-column>
            <el-table-column label="公司名称" prop="companyName" align="center" width="220"></el-table-column>
            <el-table-column label="费用发生时间" prop="costTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.costTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="付款时间" prop="paymentTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="确认时间" prop="confirmTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.confirmTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="结算时间" prop="settlementTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.settlementTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="费用金额(USD)" prop="costPriceUSD" align="right" width="180">
                <template slot-scope="scope">{{(scope.row.costPriceUSD/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="结算日汇率" prop="exchangeRate" align="right" width="120"></el-table-column>
            <el-table-column label="费用金额(CNY)" prop="costPriceCNY" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.costPriceCNY/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="费用项目类型" prop="costType" align="right" width="120" :formatter="format_status" show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="费用单状态" prop="status" align="center" width="120">
                <template slot-scope="scope">{{scope.row.status|status }}</template>
            </el-table-column>
            <el-table-column label="关联订单号" prop="associatedOrderCode" align="center" width="120" show-overflow-tooltip> </el-table-column>
            <el-table-column label="结算单号" prop="settlementCode" align="center" width="150"> </el-table-column>
            <el-table-column label="备注" prop="remark" align="center" width="120"> </el-table-column>
            <el-table-column label="更新人" prop="lastOptUser" align="center" width="150"> </el-table-column>
            <el-table-column label="操作" fixed="right" width="180">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="addForm_btn(scope.row.costCode)">修改</el-button>
                    <el-button type="danger" size="mini" @click="costDelete(scope.row.costCode)" v-if="scope.row.status==1||scope.row.status==6">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>
<style lang="less">
.add-form {
    .el-dialog__body {
        height: 400px;
        overflow-y: auto;
    }
    .el-dialog--small {
        width: 40%;
    }
    .el-dialog {
        margint-bottom: 30px;
    }
    .el-input__inner {
        width: 300px;
    }
    .el-date-editor.el-input {
        width: 300px;
    }
}
</style>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                costCode: '',
                supplierCode: '',
                companyName: '',
                costType: '',
                settlementCode: '',
                status: '',
                costTimeStart: '',
                costTimeEnd: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }

                }
            },
            //报表列表
            loading: false,
            total: 0,
            reportList: [],
             fileList: [],
            //新增菜单
            dialogAddForm: false,
            addForm: {
                companyName: "",
                costPriceCNY: "",
                costPriceCNY_c: "",
                costPriceUSD: "",
                costPriceUSD_c: "",
                costTime: "",
                costType: '',
                exchangeRate: '',
                status: '',
                supplierCode: "",
                costCode: ""
            },
            //选择数据
            selectData: [],
            selectIds: [],
        }
    },
    filters: {
        status(status) {
            switch (status) {
                case 1:
                    return '待审核';
                    break;
                case 2:
                    return '待商家确认';
                    break;
                case 3:
                    return '待结算';
                    break;
                case 4:
                    return '已结算';
                    break;
                case 5:
                    return '付款完成';
                    break;
                case 6:
                    return '审核不通过';
                    break;
                case 7:
                    return '确认失败';
                    break;
               
            }
        },
        dateFormat(date) {
            return date?format('yyyy-MM-dd hh:mm', new Date(date)):'';
        }
    },
    methods: {
        //列表状态过滤
        format_status(row, column, cellValue) {
            for (let i in this.myConfig.status[column.property]) {
                if (this.myConfig.status[column.property][i].val == row[column.property]) {
                    return this.myConfig.status[column.property][i].label;
                    break;
                }
            }
            return row[column.property];
        },
        DateToString() {
            for (let i in this.searchForm) {
                //日期转换
                if (this.searchForm[i] instanceof Date) {
                    this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
                }
                //去掉前后空格
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/financial/cost', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },
        doUpdate(data) {
        },
        //增加按钮
        addForm_btn(id) {
            this.dialogAddForm = true;
            if (id) {//修改
                this.axios.post(this.myConfig.code.proxy + '/report/api/report/financial/cost', { body: { costCode: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code == '10000') {
                        this.addForm = data.body.rows[0];
                        this.addForm.costPriceCNY_c=this.addForm.costPriceCNY/100;
                        this.addForm.costPriceUSD_c=this.addForm.costPriceUSD/100;
                        this.addForm.costType = this.addForm.costType
                        this.addForm.status = this.addForm.status.toString();
                    }
                })
            } else {//添加
                this.addForm = {
                    companyName: "",
                    costPriceCNY: "",
                    costPriceCNY_c: "",
                    costPriceUSD: "",
                    costPriceUSD_c: "",
                    costTime: "",
                    costType: '',
                    exchangeRate: '',
                    status: '',
                    supplierCode: "",
                    costCode: ""
                }
            }


        },
        //添加表单提交
        submit_add() {
            this.$refs.addForm.validate((validate) => {
                if (validate) {
                    this.addForm.costPriceCNY=parseFloat(this.addForm.costPriceCNY_c)*100;
                    this.addForm.costPriceUSD=parseFloat(this.addForm.costPriceUSD_c)*100;
                    this.axios.post(this.myConfig.code.proxy + '/report/api/financial/save/cost', { body: this.addForm }).then(response => {
                        let data = response.data;
                        if (data.head.code == '10000') {
                            this.$message({
                                type: "success",
                                message: "操作成功",
                                duration: 1000
                            })
                            this.dialogAddForm = false;
                            this.getReportList();
                        }
                    })
                }
            })

        },
        //数据导出
        export_smt() {
            this.DateToString();
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj) {
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        str.push(p + "=" + obj[p]);
                    }
                }
                return str.join("&");
            }
            window.open( '/proxy/report/api/export/financial/cost?' + serialize(tmp));
        },
        //数据导入
        doImport(res, file, fileList) {
            if (res.head.code == "10000") {
                this.$message({
                    type: 'success',
                    message: '导入成功'
                })
            } else {
                for (let i in fileList) {
                    if (file.uid == fileList[i].uid) {
                        fileList.splice(i, 1);
                    }
                }
                this.$message({
                    type: 'error',
                    message: res.body.sub_msg
                })
            }
            this.getReportList();
        },
        //复选框
        handleSelectionChange(val) {
            this.selectData = val;
        },
        //审核
        approval(id,status) {
            if (id) {
                this.selectIds = [id]
            } else {
                let ids = [];
                for (let i in this.selectData) {
                    ids.push(this.selectData[i].costCode)
                }
                this.selectIds = ids.concat([]);
            }
            if (this.selectIds.length == 0) {
                this.$message({
                    message: '请选择结算单',
                    type: 'error'
                })
                return false;
            }
            this.axios.post(this.myConfig.code.proxy + '/report/api/financial/approval/cost', { body: { costCodes: this.selectIds, status: status } }).then((res) => {
                let data = res.data;
                if (data.head.code == '10000') {
                    this.$message({
                        message: '操作成功',
                        type: 'success'
                    })
                    this.getReportList();
                }
            })
        },
        costDelete(id){
            console.log(111111111111111)
            if (id) {
                this.selectIds = [id]
            } else {
                let ids = [];
                for (let i in this.selectData) {
                    ids.push(this.selectData[i].costCode)
                }
                this.selectIds = ids.concat([]);
            }
            if (this.selectIds.length == 0) {
                this.$message({
                    message: '请选择结算单',
                    type: 'error'
                })
                return false;
            }
            this.$confirm('您确定要删除该商品？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {         
                this.axios.post('/proxy/report/api/financial/delete/cost',{body:{costCodes:this.selectIds}}).then(res=>{
                    let data = res.data;
                    this.$message({
                        message: data.head.code == "10000" ? "操作成功" : data.body.sub_msg,
                        type: data.head.code == "10000" ? "success" : "error"
                    });
                    if(data.head.code =='10000'){
                    this.getReportList();
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });            
            
        },
        
    },
    created() {
        this.getReportList();
    }
}
</script>




