<template>
  <div class="wrap">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>运营商报表</el-breadcrumb-item>
        <el-breadcrumb-item>商品销售明细报表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!--搜索-->
    <div class="search report">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
        <div class="fold">
          <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
            <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
          </span>
        </div>
        <transition name="el-zoom-in-center">
          <div class="item-list" v-if="fold">
            <el-form-item label="下单时间始">
              <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeStart" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item label="下单时间止" class="">
              <!-- <i class="iconfont icon-double-arrow"></i> -->
              <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item label="支付时间始">
              <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeStart" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item label="支付时间止" class="">
              <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item label="订单号">
              <el-input v-model="searchForm.orderCode" placeholder="订单号" size="small"></el-input>
            </el-form-item>
            <el-form-item label="订单来源号">
              <el-input v-model="searchForm.sourceOrderCode" placeholder="订单号" size="small"></el-input>
            </el-form-item>
            <el-form-item label="订单状态">
              <el-select v-model="searchForm.orderStatus" placeholder="订单状态" size="small" clearable>
                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.orderStatus" :key="item.val"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="商家编号">
              <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
            </el-form-item>
            <el-form-item label="流量来源">
              <el-select v-model="searchForm.orderSource" placeholder="流量来源" size="small" clearable>
                <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="品牌ID">
              <el-input v-model="searchForm.brandId" placeholder="品牌ID" size="small"></el-input>
            </el-form-item>
            <el-form-item label="品牌名称">
              <el-input v-model="searchForm.brandName" placeholder="品牌名称" size="small"></el-input>
            </el-form-item>
            <el-form-item label="分类">
              <el-cascader placeholder="分类" :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" clearable></el-cascader>
            </el-form-item>
            <el-form-item label="SPU编号">
              <el-input v-model="searchForm.goodsSn" :rows="1" placeholder="SPU编号" size="small" type="textarea"></el-input>
            </el-form-item>
            <el-form-item label="SKU编号">
              <el-input v-model="searchForm.productSn" :rows="1" placeholder="SKU编号" size="small" type="textarea"></el-input>
            </el-form-item>
            <el-form-item label="SPU ID">
                <el-input v-model="searchForm.goodsId"  :rows="1" type="textarea" size="small"></el-input>
            </el-form-item>
            <el-form-item class="search-btn">
              <el-button type="primary" @click="search_smt">查询</el-button>
              <el-button type="primary" @click="export_smt">
                <i class="iconfont icon-export"></i>导出</el-button>
            </el-form-item>

          </div>
        </transition>

      </el-form>
    </div>
    <!--列表-->
    <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
      <el-table-column label="订单号" prop="orderCode" align="center" width="180"></el-table-column>
      <el-table-column label="订单创建时间" prop="orderTime" align="center" width="160">
        <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
      </el-table-column>
      <el-table-column label="支付时间" prop="paymentTime" align="center" width="160">
        <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
      </el-table-column>
      <el-table-column label="订单状态" prop="orderStatus" align="center" width="100">
        <template slot-scope="scope">{{scope.row.orderStatus|orderStatus }}</template>
      </el-table-column>
      <el-table-column label="支付方式" prop="paymentChannelCode" align="center" width="150"></el-table-column>
      <el-table-column label="商家编码" prop="supplierCode" align="center" width="150"> </el-table-column>
      <el-table-column label="订单来源号" prop="sourceOrderCode" align="center" width="120" show-overflow-tooltip></el-table-column>
      <el-table-column label="流量来源" prop="orderSourceCode" align="center" width="120" show-overflow-tooltip></el-table-column>
      <el-table-column label="一级类目" prop="oneCatName" align="center" width="200" show-overflow-tooltip> </el-table-column>
      <el-table-column label="二级类目" prop="twoCatName" align="center" width="200" show-overflow-tooltip> </el-table-column>
      <el-table-column label="三级类目" prop="threeCatName" align="center" width="200" show-overflow-tooltip> </el-table-column>
      <el-table-column label="四级类目" prop="fourCatName" align="center" width="200" show-overflow-tooltip> </el-table-column>
      <el-table-column label="SPU编号" prop="goodsSn" align="center" width="150" show-overflow-tooltip> </el-table-column>
      <el-table-column label="SPU ID" prop="goodsId" align="center" width="150" show-overflow-tooltip> </el-table-column>
      <el-table-column label="SKU编号" prop="productSn" align="center" width="150" show-overflow-tooltip> </el-table-column>
      <el-table-column label="SPU标题" prop="goodsTitle" align="center" width="200" show-overflow-tooltip></el-table-column>
      <el-table-column label="SPU俄文标题" prop="goodsTitleRu" align="center" width="200" show-overflow-tooltip></el-table-column>
      <el-table-column label="SKU售价" prop="price" align="center" width="100">
        <template slot-scope="scope">{{scope.row.price/100}}</template>
      </el-table-column>
      <el-table-column label="销售数量" prop="qty" align="center" width="120"></el-table-column>
      <el-table-column label="商品运费(USD)" prop="skuFreight" align="left" width="100">
        <template slot-scope="scope">{{scope.row.skuFreight/100}}</template>
      </el-table-column>
      <el-table-column label="订单金额" prop="orderAmount" align="center" width="100">
        <template slot-scope="scope">{{scope.row.orderAmount/100}}</template>
      </el-table-column>
      <el-table-column label="支付金额" prop="payAmount" align="center" width="100">
        <template slot-scope="scope">{{scope.row.payAmount/100}}</template>
      </el-table-column>
      <el-table-column label="用户email" prop="email" align="center" width="250"></el-table-column>
      <el-table-column label="国家" prop="country" align="center" width="100"></el-table-column>
      <el-table-column label="城市" prop="province" align="center" width="150" show-overflow-tooltip></el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
      <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

  </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        orderTimeStart: '',
        orderTimeEnd: '',
        paymentTimeStart: '',
        paymentTimeEnd: '',
        orderCode: '',
        orderStatus: '',
        goodsSn: '',
        goodsId: '',
        supplierCode: '',
        companyName: '',
        orderSource: '',
        companyName: '',
        oneCatId: '',
        twoCatId: '',
        threeCatId: '',
        fourCatId: '',
        sourceOrderCode:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time
          } else {
            return false;
          }

        }
      },
      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      // 合作商列表
      cpaList: [],
      //商品分类列表
      cateList: [],
      props: {
        value: 'catId',
        label: 'catName'
      }

    }
  },
  filters: {
    orderStatus(status) {
      switch (status) {
        case 0:
          return '待支付';
          break;
        case 1:
          return '已取消';
          break;
        case 2:
          return '待发货';
          break;
        case 3:
          return '已打包';
          break;
        case 4:
          return '已发货';
          break;
        case 5:
          return '已签收';
          break;

        default:
          return '未知';
          break;
      }
    },
    dateFormat(date) {
      return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
    }
  },
  methods: {
    search_smt() {
      this.searchForm.page.page = 1;
      //去掉前后空格
      for (let i in this.searchForm) {
        if (typeof this.searchForm[i] == 'string') {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
      this.getReportList();
    },
    //报表列表获取
    DateToString() {
      for (let i in this.searchForm) {
        //日期转换
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
        }
        //去掉前后空格
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    getReportList() {
      this.loading = true;
      this.DateToString();
      this.axios.post(this.myConfig.code.proxy + '/report/api/report/operation/order_detail', { body: this.searchForm }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.loading = false;
          this.reportList = data.body.rows;
          this.total = data.body.total;
        }
      })
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    getCpaList() {
      this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: {} }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.cpaList = data.body.list;
        }
      })
    },
    export_smt() {
      this.DateToString();
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj)
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" +  encodeURIComponent(obj[p]));
          }
        return str.join("&");
      }
       window.open('/proxy/report/api/export/operation/order_detail?' + serialize(tmp));
    },
    //获取商品分类列表
    getCateList() {
      this.axios.post(this.myConfig.code.proxy + '/report/api/common/category/options', { body: {} }).then(response => {
        let data = response.data;
        if (data.head.code == '10000') {
          this.cateList = data.body;
        }
      })
    },

    //选择分类
    cateChange(change) {
      this.searchForm.oneCatId=''; 
            this.searchForm.twoCatId='';
            this.searchForm.threeCatId='';  
            this.searchForm.fourCatId='';  
      for (let [index, item] of change.entries()) {
        switch (index) {
          case 0:
            this.searchForm.oneCatId = item;
            break;
          case 1:
            this.searchForm.twoCatId = item;
            break;
          case 2:
            this.searchForm.threeCatId = item;
            break;
          case 3:
            this.searchForm.fourCatId = item;
            break;
        }
      }

    }

  },
  created() {
    this.getCateList();
    this.getCpaList();
    this.getReportList();
  }
}
</script>




