<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务结算</el-breadcrumb-item>
                <el-breadcrumb-item>结算单</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="120px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="结算单号">
                            <el-input v-model="searchForm.settlementCode" placeholder="结算单号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="付款单号">
                            <el-input v-model="searchForm.paymentCode" placeholder="付款单号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="公司名称">
                            <el-input v-model="searchForm.companyName" placeholder="公司名称" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="状态">
                            <el-select v-model="searchForm.status" placeholder="状态" size="small" clearable>
                                
                                <el-option :value="item.key" :label="item.val" v-for="(item,key) in status" :key="key"></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="结算月份">
                            <el-date-picker type="month" placeholder="选择日期" v-model="searchForm.settlementMonth" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="结算单生成时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.buildTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="结算单生成时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.buildTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>
            </el-form>
        </div>
        <!-- 批量操作 -->
        <div class="doAction">
            <el-button type="primary" @click="approval('')" size="small">批量审核</el-button>
            <el-button type="primary" @click="save('')" size="small">批量生成付款申请单</el-button>
        </div>
        <!--列表-->
        <el-table :data="reportList"  style="width: 100%" v-loading="loading" class="report" max-height="520" @selection-change="handleSelectionChange"  stripe highlight-current-row>
            <el-table-column type="selection" width="40" ></el-table-column> 
            <el-table-column label="生成加成结算时间" prop="settlementMonth" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="商家编号" prop="supplierCode" align="center" min-width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="公司名称" prop="companyName" align="center" min-width="180" show-overflow-tooltip> </el-table-column>
            <el-table-column label="状态" prop="status" align="center" width="100">
                <template slot-scope="scope">{{getStatus(scope.row.status)}}</template>
            </el-table-column>   
            <el-table-column :label="'本期需结算\t货款(USD)'"  align="right" width="100"> 
                 <template slot-scope="scope">{{(scope.row.totalAmount/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="本期需收取佣金(USD)"  prop="totalCommission" align="right" width="100"> 
                 <template slot-scope="scope">{{(scope.row.totalCommission/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="本期需扣除费用(USD)" prop="totalCost" align="right" width="100"> 
                 <template slot-scope="scope">{{(scope.row.totalCost/100).toFixed(2)}}</template>
            </el-table-column>
             <el-table-column label="本期应付款USD(不含运费)" prop="totalPaymentUSD" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.totalPaymentUSD/100).toFixed(2)}}</template>
            </el-table-column>
            
            <el-table-column label="应付款CNY(不含运费)" prop="totalPaymentCNY" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.totalPaymentCNY/100).toFixed(2)}}</template>
            </el-table-column> 
            <el-table-column label="本期需扣除运费(CNY)" prop="totalFreight" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.totalFreight/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="金额调整(CNY)" prop="adjustmentAmount" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.adjustmentAmount/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="应付款(CNY)" prop="totalPayment" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.totalPayment/100).toFixed(2)}}</template>
            </el-table-column>       
            
             
            <el-table-column label="操作" width="90" fixed="right">
                <template slot-scope="scope">
                   <el-button type="primary" size="mini" @click="showDetail(scope.row,scope)">详情</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 结算单详情 -->
        <el-dialog title="结算单详情"  :visible.sync="dialogSettleDetail" fullscreen>
            <el-table :data="setttleDetail" class="report">
                <el-table-column label="结算单号" prop="settlementCode" align="center" min-width="180" show-overflow-tooltip></el-table-column>
                <el-table-column label="结算月份" prop="settlementMonth" align="center" width="180" show-overflow-tooltip></el-table-column>
                <el-table-column label="商家编号" prop="supplierCode" align="center" min-width="180" show-overflow-tooltip></el-table-column>
                <el-table-column label="公司名称" prop="companyName" align="center" min-width="180" show-overflow-tooltip> </el-table-column>
                <el-table-column label="本期需结算货款(USD)"  align="right" width="100"> 
                    <template slot-scope="scope">{{(scope.row.totalAmount/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="本期需收取佣金(USD)"  prop="totalCommission" align="right" width="100"> 
                    <template slot-scope="scope">{{(scope.row.totalCommission/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="本期需扣除费用(USD)" prop="totalCost" align="right" width="100"> 
                    <template slot-scope="scope">{{(scope.row.totalCost/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="本期应付款USD(不含运费)" prop="totalPaymentUSD" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.totalPaymentUSD/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="结算日本位币汇率" prop="exchangeRate" align="center" width="100"> </el-table-column>
                <el-table-column label="应付款CNY(不含运费)" prop="totalPaymentCNY" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.totalPaymentCNY/100).toFixed(2)}}</template>
                </el-table-column> 
                <el-table-column label="本期需扣除运费(CNY)" prop="totalFreight" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.totalFreight/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="金额调整(CNY)" prop="adjustmentAmount" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.adjustmentAmount/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="应付款(CNY)" prop="totalPayment" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.totalPayment/100).toFixed(2)}}</template>
                </el-table-column>           
                <el-table-column label="状态" prop="status" align="center" width="100">
                    <template slot-scope="scope">{{getStatus(scope.row.status)}}</template>
                </el-table-column>
                <el-table-column label="付款金额结算币(CNY)" prop="paymentAmount" align="right" width="100">
                    <template slot-scope="scope">{{(scope.row.paymentAmount/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column label="结算单审批人" prop="auditUser" align="center" width="120"> </el-table-column>
                <el-table-column label="结算单审批时间" prop="paymentTime" align="center" width="160">
                    <template slot-scope="scope">{{scope.row.auditTime|dateFormat }}</template>
                </el-table-column>
                <el-table-column label="商家确认时间" prop="createPaymentTime" align="center" width="160">
                    <template slot-scope="scope">{{scope.row.confirmTime|dateFormat }}</template>
                </el-table-column>
                <el-table-column label="付款单号" prop="paymentCode" align="center" width="160">
                </el-table-column>               
                <el-table-column label="生成付款单时间" prop="createPaymentTime" align="center" width="160">
                    <template slot-scope="scope">{{scope.row.createPaymentTime|dateFormat }}</template>
                </el-table-column>
                <el-table-column label="付款时间" prop="paymentTime" align="center" width="160">
                    <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
                </el-table-column>      
                <el-table-column label="备注" prop="remark" align="center" width="180" :show-overflow-tooltip="true"> </el-table-column>
                <el-table-column label="操作" width="150" fixed="right">
                    <template slot-scope="scope">
                        <!-- <el-button  type="primary" size="mini" @click="save(scope.row.settlementCode)" v-if="scope.row.status=='3'"  >付款单</el-button> -->
                        <!-- <el-button  type="primary" size="mini"  @click="approval(scope.row.settlementCode)" v-if="scope.row.status=='1' ||scope.row.status=='7'">审核</el-button> -->
                        <el-button  type="primary" size="mini"  @click="adjust(scope.row)" v-if="!(scope.row.status=='3' || scope.row.status=='4'|| scope.row.status=='5')">调整金额</el-button >
                    </template>
                </el-table-column>      
            </el-table>
        </el-dialog>

        <!--新增表单-->
        <el-dialog title="结算单金额调整" :visible.sync="dialogAdjust" class="add-form" :close-on-click-modal="false" >
            <el-form :model="adjustForm" label-width="120px" ref="addForm">
                <el-form-item label="结算单号"  >
                    <el-input v-model="adjustForm.settlementCode" placeholder="费用单名称" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="结算月份"  >
                    <el-input v-model="adjustForm.settlementMonth" placeholder="费用单名称" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="商家编号">
                    <el-input v-model="adjustForm.supplierCode" placeholder="费用单名称" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="公司名称"  >
                    <el-input v-model="adjustForm.companyName" placeholder="费用单名称" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="应付款(CNY)"  >
                    <el-input :value="(adjustForm.totalPayment/100).toFixed(2)" placeholder="费用单名称" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="金额调整(CNY)"  >
                    <el-input v-model="adjustForm.adjustmentAmount_c" @blur="parseNum" ></el-input>
                    
                </el-form-item>
                <el-form-item label="备注" >
                    <el-input v-model="adjustForm.remark"  type="textarea" ></el-input>
                </el-form-item>
                <el-form-item label="提示"  v-if="adjustVaidate" >
                    <div style="color:#ff4949">金额调整与备注必须填写且金额为数值</div>
                </el-form-item>
                
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAdjust = false">取 消</el-button>
                <el-button type="primary" @click="submit_adjust">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
    .doAction {
        padding-bottom: 12px;
    }
}
</style>
<style lang="less">
.el-form-item {
    margin-bottom: 6px;
}
</style>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                settlementCode: '',
                paymentCode: '',
                supplierCode: '',
                status: '',
                companyName: '',
                buildTimeStart: '',
                buildTimeEnd: '',
                settlementMonth:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }

                }
            },
            //结算单状态
            status: [{
                key: 1,
                val: '待审核'
            }, {
                key: 2,
                val: '待商家确认'
            }, {
                key: 3,
                val: '可结算'
            }, {
                key: 4,
                val: '已结算'
            }, {
                key: 5,
                val: '付款完成'
            }, {
                key: 6,
                val: '审核不通过'
            }, {
                key: 7,
                val: '商家确认不通过'
            }],
            //报表列表
            loading: false,
            total: 0,
            reportList: [],
            // 合作商列表
            cpaList: [],
            //选择数据
            selectData: [],
            selectIds: [],

            //调整结算单金额
            adjustForm:{adjustmentAmount_c:"",remark:""},
            dialogAdjust:false,
            adjustVaidate:false,
            // 结算单详情
            dialogSettleDetail:false,
            setttleDetail:[],
            setttleDetailIndex:''

        }
    },
    computed: {

    },
    filters: {
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {
        DateToString() {
            for (let i in this.searchForm) {
                //日期转换
                if (this.searchForm[i] instanceof Date) {
                    if(i=='settlementMonth'){
                        this.searchForm[i] = format('yyyy-MM', new Date(this.searchForm[i]));
                    }else{
                        this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
                    }
                }
                //去掉前后空格
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/financial/settlement/package', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                    if(this.reportList[this.setttleDetailIndex]){
                         this.setttleDetail=this.reportList[this.setttleDetailIndex].packageLst;
                    }
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },
        doAction(data) {
        },
        getStatus(data) {
            for (let i in this.status) {
                if (this.status[i].key == data) {
                    return this.status[i].val
                }
            }
        },
        //操作
        //审核
        approval(id) {
            if (id) {
                this.selectIds = [id]
            } else {
                let ids = [];
                for (let i in this.selectData) {
                    ids=ids.concat(this.selectData[i].settlementCodes);
                }
                this.selectIds = ids.concat([]);
            }
            if (this.selectIds.length == 0) {
                this.$message({
                    message: '请选择结算单',
                    type: 'error'
                })
                return false;
            }
            this.axios.post(this.myConfig.code.proxy + '/report/api/financial/approval/settlement', { body: { settlementCodes: this.selectIds, status: '2' } }).then((res) => {
                let data = res.data;
                if (data.head.code == '10000') {
                    this.$message({
                        message: '操作成功',
                        type: 'success'
                    })
                    this.getReportList();
                }
            })
        },
        //生成结算单
        save(id) {
            if (id) {
                this.selectIds = [id]
            } else {
                let ids = [];
                for (let i in this.selectData) {
                    ids=ids.concat(this.selectData[i].settlementCodes);
                }
                this.selectIds = ids.concat([]);
            }
            if (this.selectIds.length == 0) {
                this.$message({
                    message: '请选择结算单',
                    type: 'error'
                })
                return false;
            }
            this.axios.post(this.myConfig.code.proxy + '/report/api/financial/save/paymentApply', { body: { settlementCodes: this.selectIds } }).then((res) => {
                let data = res.data;
                if (data.head.code == '10000') {
                    this.$message({
                        message: '操作成功',
                        type: 'success'
                    })
                    this.getReportList();
                }
            })
        },
        //复选框
        handleSelectionChange(val) {
            this.selectData = val;
        },
        //数据导出
        export_smt() {
            this.DateToString();
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj) {
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        str.push(p + "=" + obj[p]);
                    }
                }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/financial/settlement?' + serialize(tmp));
        },

        // 结算单金额调整
        adjust(data){
            this.adjustVaidate=false;
            let _data=data;
            _data.adjustmentAmount_c='';
            if(!_data.remark){
                _data.remark=''
            }
            this.adjustForm=JSON.parse(JSON.stringify(_data));
           
            this.adjustForm.adjustmentAmount_c=this.adjustForm.adjustmentAmount/100;
            this.dialogAdjust=true;
        },
        submit_adjust(){
            
            if(this.adjustForm.remark==''||this.adjustForm.adjustmentAmount_c==''){
                this.adjustVaidate=true;
                return false;
            }else{
                this.adjustVaidate=false;
            }
            this.adjustForm.adjustmentAmount=parseFloat(this.adjustForm.adjustmentAmount_c)*100;
            this.axios.post('/proxy/report/api/financial/adjust/settlement',{body:this.adjustForm}).then(res=>{
                let data=res.data;
                this.$message({
                    message: data.head.code == "10000"?'操作成功':data.body.sub_msg,
                    type: data.head.code == "10000" ? "success" : "error"
                });
                if (data.head.code == "10000") {
                    this.dialogAdjust=false;
                    this.getReportList();
                }
            })
        
            
        },
        parseNum(){
            this.adjustForm.adjustmentAmount_c=parseFloat(this.adjustForm.adjustmentAmount_c)?parseFloat(this.adjustForm.adjustmentAmount_c):'';
        },
        // 结算单详情
        showDetail(row,scope){
           
            this.dialogSettleDetail=true;
            this.setttleDetailIndex=scope.$index;
            console.log(this.setttleDetailIndex)
            this.setttleDetail=this.reportList[scope.$index].packageLst;
        }

    },
    created() {
        this.getReportList();
    }
}
</script>




