<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务结算</el-breadcrumb-item>
                <el-breadcrumb-item>财务佣金收入</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <!-- <i class="el-icon-search"></i> -->
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                       <el-form-item label="包裹单号">
                            <el-input v-model="searchForm.packageCode"  size="small"></el-input>
                       </el-form-item>
                       <el-form-item label="结算单号">
                            <el-input v-model="searchForm.settlementCode" placeholder="结算单号" size="small"></el-input>
                       </el-form-item>
                       <el-form-item label="包裹单状态">
                            <el-select v-model="searchForm.packageStatus" size="small" clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.packageStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="订单号">
                            <el-input v-model="searchForm.orderCode" placeholder="订单号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="结算状态">
                            <el-select v-model="searchForm.settlementStatus" size="small" clearable>
                                
                                <el-option value="1" label="待结算"></el-option>
                                <el-option value="2" label="可结算"></el-option>
                                <el-option value="3" label="已结算"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="商品编码">
                            <el-input v-model="searchForm.goodsSn" placeholder="商品编码" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="公司名称">
                            <el-input v-model="searchForm.companyName" placeholder="公司名称" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="流量来源">
                            <el-select v-model="searchForm.orderSource" placeholder="结算状态" size="small" clearable>
                                
                                <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="下单时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="下单时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="支付时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="支付时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column type="index" width="80" fixed="left"></el-table-column>        
            <el-table-column label="订单创建时间" prop="orderTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="支付时间" prop="paymentTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="包裹单号" prop="packageCode" align="center" width="180"></el-table-column>
            <el-table-column label="包裹单状态" prop="packageStatus" align="center" width="120" :show-overflow-tooltip="true" :formatter="format_status"></el-table-column>
            <el-table-column label="订单号" prop="orderCode" align="center" width="180"></el-table-column>
            <el-table-column label="结算状态" prop="settlementStatus" align="center" width="100">
                <template slot-scope="scope">{{scope.row.settlementStatus|renderStatus }}</template>
            </el-table-column>
            <el-table-column label="支付方式" prop="paymentChannelCode" align="center" width="150"></el-table-column>
            <el-table-column label="单据类型" prop="documentsType" align="center" width="100">
                <template slot-scope="scope">{{scope.row.documentsType|documentsType }}</template>
            </el-table-column>
            <el-table-column label="流量来源" prop="orderSourceCode" align="center" width="100" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column label="商品编码" prop="goodsSn" align="center" width="180" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="商家编码" prop="supplierCode" align="center" width="180" :show-overflow-tooltip="true">  </el-table-column>
            <el-table-column label="公司名称" prop="companyName" align="center" width="150" :show-overflow-tooltip="true" ></el-table-column>
            <el-table-column label="一级品类名" prop="oneCatName" align="center" width="180" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="二级品类名" prop="twoCatName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="三级品类名" prop="threeCatName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="品牌名称" prop="brandName" align="center" width="120" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="商品售价" prop="price" align="right" width="100"> 
                 <template slot-scope="scope">{{(scope.row.price/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="数量" prop="qty" align="center" width="120"> </el-table-column>
            <el-table-column label="商品运费(USD)" prop="skuFreight" align="left" width="120">
              <template slot-scope="scope">{{scope.row.skuFreight/100}}</template>
            </el-table-column>
            <el-table-column label="总金额" prop="totalPrice" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.totalPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="扣率" prop="commissionRate" align="right" width="120"> 
                <template slot-scope="scope">{{(scope.row.commissionRate).toFixed(2)}}%</template>
            </el-table-column>
            <el-table-column label="佣金" prop="commission" align="right" width="120">
                <template slot-scope="scope">{{(scope.row.commission/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="应付款" prop="actualPayment" align="right" width="120"> 
                 <template slot-scope="scope">{{(scope.row.actualPayment/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="结算单号" prop="settlementCode" align="center" width="100"></el-table-column>
            <el-table-column label="操作" min-width="100">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doAction(scope.row.acceptUser)">详情</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        orderTimeStart: "",
        orderTimeEnd: "",
        paymentTimeStart: "",
        paymentTimeEnd: "",
        orderCode: "",
        settlementStatus: "",
        goodsSn: "",
        supplierCode: "",
        companyName: "",
        orderSource: "",
        companyName: "",
        packageStatus:'',
        settlementCode:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      // 合作商列表
      cpaList: []
    };
  },
  filters: {
    renderStatus(status) {
      switch (status) {
        case 1:
          return "待结算";
          break;
        case 2:
          return "可结算";
          break;
        case 3:
          return "已结算";
          break;
        case 0:
          return "未知";
          break;
      }
    },
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    },
    documentsType(data) {
      switch (data) {
        case 1:
          return "销售出库";
          break;
        case 2:
          return "退货入库";
          break;
        case 3:
          return "拒收入库";
          break;
        case 0:
          return "未知";
          break;
      }
    }
  },
  methods: {
    //列表状态过滤
    format_status(row, column, cellValue) {
        for (let i in this.myConfig.status[column.property]) {
            if (this.myConfig.status[column.property][i].val == row[column.property]) {
                return this.myConfig.status[column.property][i].label;
                break;
            }
        }
        return row[column.property];
    },
    DateToString() {
      for (let i in this.searchForm) {
        //日期转换
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format(
            "yyyy-MM-dd",
            new Date(this.searchForm[i])
          );
        }
        //去掉前后空格
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //报表列表获取
    getReportList() {
      this.loading = true;
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/report/financial/commission",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    doAction(data) {},
    getCpaList() {
      this.axios
        .post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cpaList = data.body.list;
          }
        });
    },
    //数据导出
    export_smt() {
      this.DateToString();
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj) {
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" + obj[p]);
          }
        }
        return str.join("&");
      }
    
         window.open("/proxy/report/api/export/financial/commission?" + serialize(tmp));
    }
  },
  created() {
    this.getCpaList();
    this.getReportList();
  }
};
</script>




