<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务结算</el-breadcrumb-item>
                <el-breadcrumb-item>付款申请单</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="付款单号">
                            <el-input v-model="searchForm.paymentCode" placeholder="付款单号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="公司名称">
                            <el-input v-model="searchForm.companyName" placeholder="公司名称" size="small"></el-input>
                        </el-form-item>
    
                        <el-form-item label="状态">
                            <el-select v-model="searchForm.status" placeholder="状态" size="small" clearable>            
                                <el-option value="1" label="待付款"></el-option>
                                <el-option value="2" label="已付款"></el-option>
                            </el-select>
                        </el-form-item>
    
                        <el-form-item label="申请付款时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.applyPaymentTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="申请付款时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.applyPaymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                             <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>
    
            </el-form>
        </div>
        <!-- 批量操作 -->
        <div class="doAction">
            <el-button type="primary" @click="doAction('batch')" size="small">批量付款</el-button>
        </div>
        <!--列表-->
        <el-table :data="reportList"  style="width: 100%" v-loading="loading" class="report" max-height="520" border stripe highlight-current-row @selection-change="handleSelectionChange" >
            <el-table-column type="selection" width="40" fixed="left" :selectable="selectable"></el-table-column>
            <el-table-column label="付款单号" prop="paymentCode" align="center" width="150" fixed="left"></el-table-column>
            <el-table-column label="申请付款时间" prop="applyPaymentTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.applyPaymentTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="生成时间" prop="buildTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.buildTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="商家编号" prop="supplierCode" align="center" width="180"show-overflow-tooltip></el-table-column>
            <el-table-column label="公司名称" prop="companyName" align="center" width="180" show-overflow-tooltip> </el-table-column>
            <el-table-column label="状态" prop="status" align="center" width="100">
                <template slot-scope="scope">{{scope.row.status==1?'待付款':'已付款' }}</template>
            </el-table-column>
            <el-table-column label="应付金额(CNY)"  align="right" width="150">
                <template slot-scope="scope">{{scope.row.totalPayment?(scope.row.totalPayment/100).toFixed(2):''}}</template>
            </el-table-column>   
            <el-table-column label="申请付款人" prop="applyUser" align="center" width="150"></el-table-column>
            <el-table-column label="备注" prop="remark" align="center" width="150"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doAction(scope.row.paymentCode)" v-if="scope.row.status==1">付款</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    
    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        paymentCode: "",
        supplierCode: "",
        companyName: "",
        status: "",
        applyPaymentTimeStart: "",
        applyPaymentTimeEnd: "",
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      // 合作商列表
      cpaList: [],
      //选择数据
      selectData: []
    };
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    }
  },
  methods: {
    DateToString() {
      for (let i in this.searchForm) {
          //日期转换
          if (this.searchForm[i] instanceof Date) {
              if(i=='settlementMonth'){
                  this.searchForm[i] = format('yyyy-MM', new Date(this.searchForm[i]));
              }else{
                  this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
              }
          }
          //去掉前后空格
          if (this.searchForm[i] instanceof String) {
              this.searchForm[i] = this.searchForm[i].trim();
          }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //报表列表获取
    getReportList() {
      this.loading = true;

      if (this.searchForm.applyPaymentTimeStart != "") {
        this.searchForm.applyPaymentTimeStart = format(
          "yyyy-MM-dd",
          new Date(this.searchForm.applyPaymentTimeStart)
        );
      }
      if (this.searchForm.applyPaymentTimeEnd != "") {
        this.searchForm.applyPaymentTimeEnd = format(
          "yyyy-MM-dd",
          new Date(this.searchForm.applyPaymentTimeEnd)
        );
      }
      this.axios
        .post(
          this.myConfig.code.proxy +
            "/report/api/report/financial/paymentApply",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    doAction(type) {
      let tempArr=[];
      if(type =='batch'){
          for(let i in this.selectData){
            tempArr.push(this.selectData[i].paymentCode)
          }
      }else{
        tempArr=[type];
      }
      if (tempArr.length == 0) {
          this.$message({
              message: '请选择结算单',
              type: 'error'
          })
          return false;
      }
      this.axios.post('/proxy/report/api/financial/update/paymentApply', { body: { paymentCodes: tempArr,status:2 } }).then((res) => {
          let data = res.data;
          if (data.head.code == '10000') {
              this.$message({
                  message: '操作成功',
                  type: 'success'
              })
              this.getReportList();
          }
      })
    },

    //数据导出
    export_smt() {
        this.DateToString();
        var tmp = JSON.parse(JSON.stringify(this.searchForm));
        delete tmp.page;
        function serialize(obj) {
            var str = [];
            for (var p in obj) {
                if (obj.hasOwnProperty(p) && obj[p] != "") {
                    str.push(p + "=" + obj[p]);
                }
            }
            return str.join("&");
        }
         window.open('/proxy/report/api/export/financial/paymentApply?' + serialize(tmp));
    },
    //复选框
    handleSelectionChange(val) {
        this.selectData = val;
    },
    selectable(val,row){
        if(val.status==1){
            return true
        }else{
            return false
        }
        
    }
  },
  created() {
    this.getReportList();
  }
};
</script>




