<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>运营商报表</el-breadcrumb-item>
                <el-breadcrumb-item>商家销售列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="商家名称">
                            <el-input v-model="searchForm.supplierName" placeholder="商家编号" size="small"></el-input>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>

        </div>
        <!-- <div class="import">
            <el-upload class="upload-import" action="/proxy/report/api/import/financial/freight" :on-success="doImport" :accept="'file/file.xls'">
                <el-button size="small" type="primary">导入</el-button>
            </el-upload>
        </div> -->
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="商家编号" prop="supplierCode" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="商家名称" prop="supplierName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="累计销售数量" prop="salesQuantity" align="center" width="120"></el-table-column>
            <el-table-column label="累计销售金额" prop="salesAmount" align="center" width="120">
                <template slot-scope="scope">
                    {{scope.row.salesAmount/100}}
                </template>
            </el-table-column>
            <el-table-column label="SPU数量" prop="spuNum" align="center" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="SKU数量" prop="skuNum" align="center" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="品牌" prop="brandNames" align="center" width="220">
                <template slot-scope="scope">
                    <span @click="showInfo(scope.row.brandNames)">{{scope.row.brandNames|split }}
                        <i class="iconfont icon-more" v-if="scope.row.brandNames"></i>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="一级类目" align="center" width="180">
                <template slot-scope="scope">
                    <span @click="showInfo(scope.row.oneCatNames)">{{scope.row.oneCatNames|split }}
                        <i class="iconfont icon-more" v-if="scope.row.oneCatNames"></i>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="二级类目" prop="twoCatNames" align="center" width="180">
                <template slot-scope="scope">
                    <span @click="showInfo(scope.row.twoCatNames)">{{scope.row.twoCatNames|split }}
                        <i class="iconfont icon-more" v-if="scope.row.twoCatNames"></i>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="三级类目" prop="threeCatNames" align="center" width="180">
                <template slot-scope="scope">
                    <span @click="showInfo(scope.row.threeCatNames)">{{scope.row.threeCatNames|split }}
                        <i class="iconfont icon-more" v-if="scope.row.threeCatNames"></i>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="四级类目" prop="fourCatNames" align="center" width="180">
                <template slot-scope="scope">
                    <span @click="showInfo(scope.row.fourCatNames)">{{scope.row.fourCatNames|split }}
                        <i class="iconfont icon-more" v-if="scope.row.fourCatNames"></i>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="添加时间" prop="updateTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.updateTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="首次销售时间"  align="center" width="160">
                <template slot-scope="scope">{{scope.row.firstSalesTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="首次上传产品时间" prop="updateTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.firstUploadTime|dateFormat}}</template>
            </el-table-column>

        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                supplierCode: '',
                supplierName: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },

            //报表列表
            loading: false,
            total: 0,
            reportList: [],
        }
    },
    filters: {
        auditStatus(status) {
            switch (status) {
                case 1:
                    return '审核中';
                    break;
                case 2:
                    return '翻译中';
                    break;
                case 3:
                    return '审核不通过';
                    break;
                case 5:
                    return '审核通过';
                    break;
                case 6:
                    return '翻译驳回';
                    break;
                case 7:
                    return '未提交审核';
                    break;
                default:
                    return '未知';
                    break;
            }
        },
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        },
        split(data) {
            return data ? data.substring(0, 15) : '';
        }
    },
    methods: {

        search_smt() {
            this.searchForm.page.page = 1;
            //去掉前后空格
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/operation/supplier', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },

        export_smt() {
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj)
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        str.push(p + "=" + obj[p]);
                    }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/operation/supplier?' + serialize(tmp));
        },
        showInfo(msg) {
            if (!msg) {
                return false;
            }
            const h = this.$createElement;
            let arr = [];
            for (let i in msg.split(';')) {
                let tmp = h('p', msg.split(';')[i]);
                arr.push(tmp)
            }
            this.$notify({
                title: '明细',
                message: h('div', arr),
                offset: 100,
                customClass: "notify-report",
            });
        },
        doImport(res, file, fileList) {
            if (res.head.code == "10000") {
                this.$message({
                    type: 'success',
                    message: '导入成功'
                })
            } else {
                for(let i in fileList){
                    if(file.uid==fileList[i].uid){
                         fileList.splice(i,1);
                    }
                }
                this.$message({
                    type: 'error',
                    message: res.body.sub_msg
                })
            }
            this.getReportList();
        }


    },
    created() {
        this.getReportList();
    }
}
</script>




