<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务报表</el-breadcrumb-item>
                <el-breadcrumb-item>财务运费报表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="订单单号">
                            <el-input v-model="searchForm.orderCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="运单号">
                            <el-input v-model="searchForm.waybillNo" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="包裹单号">
                            <el-input v-model="searchForm.packageCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="包裹单状态">
                            <el-select v-model="searchForm.packageStatus" size="small" clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.packageStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="结算单号">
                            <el-input v-model="searchForm.settlementCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="结算状态">
                            <el-select v-model="searchForm.status" size="small" clearable>
                                <el-option value="" label="所有"></el-option>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.status" :key="item.val" ></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="运费导入时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.importFreightTimeStart" :picker-options="pickerOptions" :format="'yyyy-MM-dd'"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="运费导入时间止">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.importFreightTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="订单支付始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeStart" :picker-options="pickerOptions" :format="'yyyy-MM-dd'"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="订单支付止">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.paymentTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出</el-button>
                        </el-form-item>
                    </div>
                </transition>
            </el-form>
        </div>
        <div class="import">
            <el-upload class="upload-import" action="/proxy/report/api/import/financial/freight" :on-success="doImport" :accept="'file/file.xls'" :file-list="fileList">
                <el-button size="small" type="primary">导入</el-button>
            </el-upload>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" highlight-current-row border>
            <el-table-column label="订单号" prop="orderCode" align="center" width="180"></el-table-column>
            <el-table-column label="运单号" prop="waybillNo" align="center" width="180"></el-table-column>
            <el-table-column label="商家编号" prop="supplierCode" align="center" width="180"></el-table-column>
            <el-table-column label="包裹单号" prop="packageCode" align="center" width="180"></el-table-column>
            <el-table-column label="包裹单状态" prop="packageStatus" align="center" width="180" :formatter="format_status" show-overflow-tooltip></el-table-column>
            <el-table-column label="结算状态" prop="status" align="center" width="180" :formatter="format_status" show-overflow-tooltip></el-table-column>
            <el-table-column label="订单支付时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>
            
            
            <el-table-column label="订单总金额" width="100" align="right">
                <template slot-scope="scope">{{(scope.row.orderAmount/100).toFixed(2)}}</template>
            </el-table-column>
            
            <el-table-column label="是否丰卖网月结账号" width="160" align="center">
                <template slot-scope="scope">{{scope.row.isOutWaybill==0?'是':'否'}}</template>
            </el-table-column>
            <el-table-column label="折前运费金额" width="130" align="right">
                <template slot-scope="scope">{{(scope.row.totalPostage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="折扣金额" width="120" align="right">
                <template slot-scope="scope">{{(scope.row.discountPostage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="折后运费金额" width="120" align="right">
                <template slot-scope="scope">{{(scope.row.postage/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="结算单号" prop="settlementCode" align="center" width="180"></el-table-column>
            <el-table-column label="结算时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.settlementTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="计费重量(kg)" prop="feeWeight" align="center" width="180">
                <template slot-scope="scope">{{scope.row.feeWeight?(scope.row.feeWeight/1000):''}}</template>
            </el-table-column>
            <el-table-column label="产品类型" prop="postageProductType" align="center" width="180"></el-table-column>
            <el-table-column label="对方地区" prop="recipientArea" align="center" width="180"></el-table-column>
            <el-table-column label="运费导入时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.importFreightTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="创建时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.buildTime|dateFormat }}</template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                orderCode: '',
                waybillNo: '',
                supplierCode: '',
                settlementCode: '',
                status: '',
                paymentTimeStart: '',
                paymentTimeEnd: '',
                packageCode:'',
                packageStatus:'',
                importFreightTimeStart:'',
                importFreightTimeEnd:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }
                }
            },

            //报表列表
            loading: false,
            total: 0,
            reportList: [],
            fileList: [],
        }
    },
    filters: {
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {
        //列表状态过滤
        format_status(row, column, cellValue) {
            for (let i in this.myConfig.status[column.property]) {
                if (this.myConfig.status[column.property][i].val == row[column.property]) {
                    return this.myConfig.status[column.property][i].label;
                    break;
                }
            }
            return row[column.property];
        },
        //日期转换
        DateToString() {
            for (let i in this.searchForm) {
                //日期转换
                if (this.searchForm[i] instanceof Date) {
                    this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
                }
                //去掉前后空格
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/financial/freight', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },

        //数据导出
        export_smt() {
            this.DateToString();
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj)
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        if (obj[p] instanceof Array) {
                            str.push(p + "Str=" + obj[p].join());
                        } else {
                            str.push(p + "=" + obj[p]);
                        }
                    }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/financial/freight?' + serialize(tmp));
        },
        //数据导入
        doImport(res, file, fileList) {
            if (res.head.code == "10000") {
                this.$message({
                    type: 'success',
                    message: '导入成功'
                })
            } else {
                for (let i in fileList) {
                    if (file.uid == fileList[i].uid) {
                        fileList.splice(i, 1);
                    }
                }
                console.log(res,3333333)
                let _html=res.body.sub_msg;
                this.$alert(`<div>${_html}</div>`, '错误提示', {
                    dangerouslyUseHTMLString: true,
                    customClass:'message-import'
                });
                
            }
            this.getReportList();
        }


    },
    created() {
        this.getReportList();
    }
}
</script>


<style lang="less" >
.wrap {
    padding-right: 40px;
    
}
.message-import .el-message-box__message{
     
    max-height: 300px;
    overflow-y: auto;
}
</style>



