<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>客服报表</el-breadcrumb-item>
                <el-breadcrumb-item>备案商品信息</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="sku编号">
                            <el-input v-model="searchForm.productSn" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="创建时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeStart" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="创建时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>

                        </el-form-item>

                    </div>
                </transition>

            </el-form>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" :tooltip-effect="'light'" highlight-current-row border>
            <el-table-column type="index" width="70" :fixed="'left'">
            </el-table-column>
            <el-table-column label="skuId" align="center" width="" prop="productId" show-overflow-tooltip> </el-table-column>
            <el-table-column label="SKU编号" align="center" width="" prop="productSn" show-overflow-tooltip> </el-table-column>
            <el-table-column label="SKU名称" align="center" width="" prop="productName" show-overflow-tooltip> </el-table-column>
            <el-table-column label="SKU售价" align="right" width="160" prop="productPrice" show-overflow-tooltip>
                <template slot-scope="scope">{{(scope.row.productPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="SKU重量" prop="productWeight" align="center" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="商品链接" prop="productUrl" align="center" width="150" show-overflow-tooltip></el-table-column>

            <el-table-column label="生成时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.buildTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="更新时间" align="center" width="160">
                <template slot-scope="scope">{{scope.row.updateTime|dateFormat }}</template>
            </el-table-column>

        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                productSn: '',
                buildTimeStart: '',
                buildTimeEnd: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }
                }
            },
            //报表列表
            loading: false,
            total: 0,
            reportList: [],
        }
    },
    filters: {
        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {

        //列表状态过滤
        format_status(row, column, cellValue) {
            for (let i in this.myConfig.status[column.property]) {
                if (this.myConfig.status[column.property][i].val == row[column.property]) {
                    return this.myConfig.status[column.property][i].label;
                    break;
                }
            }
            return row[column.property];
        },
        search_smt() {
            this.searchForm.page.page = 1;
            this.DateToString();
            this.getReportList();
        },
        //日期转换
        DateToString() {
            for (let i in this.searchForm) {
                //日期转换
                if (this.searchForm[i] instanceof Date) {
                    this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));
                }
                //去掉前后空格
                if (this.searchForm[i] instanceof String) {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.DateToString();
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/customer/record_sku', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },

        export_smt() {
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj)
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        if (obj[p] instanceof Array) {
                            str.push(p + "Str=" + obj[p].join());
                        } else {
                            str.push(p + "=" + obj[p]);
                        }
                    }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/customer/record_sku?' + serialize(tmp));
        },

    },
    created() {
        this.getReportList();
    }
}
</script>




