<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>运营商报表</el-breadcrumb-item>
                <el-breadcrumb-item>SPU商品报表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="商家编码">
                            <el-input v-model="searchForm.supplierCode" placeholder="商家编码" size="small"></el-input>
                        </el-form-item>      
                        <el-form-item label="spu添加时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.addTimeStart" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="spu添加时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.addTimeEnd" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="上架状态">
                            <el-select v-model="searchForm.isOnSale" placeholder="上架状态" size="small" clearable>                      
                                <el-option value="0" label="下架"></el-option>
                                <el-option value="1" label="上架"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="上/下架时间始">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.onSaleTimeStart" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="上/下架时间止" class="">
                            <el-date-picker type="date" placeholder="选择日期" v-model="searchForm.onSaleTimeEnd" ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="品牌名称">
                            <el-input v-model="searchForm.brandName" placeholder="品牌名称" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="订单状态">
                            <el-select v-model="searchForm.auditStatus" placeholder="订单状态" size="small" clearable>          
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.auditStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="库存状态">
                            <el-select v-model="searchForm.isQty" placeholder="库存状态" size="small" clearable>                       
                                <el-option value="0" label="无"></el-option>
                                <el-option value="1" label="有"></el-option>
                            </el-select>
                        </el-form-item>
                        
                        <el-form-item label="分类">
                            <el-cascader placeholder="分类" :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" clearable></el-cascader>
                        </el-form-item>
                        <el-form-item label="SPU编码">
                            <el-input v-model="searchForm.goodsSn" :rows="1"  type="textarea" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="SPU ID">
                            <el-input v-model="searchForm.goodsId"  :rows="1" type="textarea" size="small"></el-input>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="商家编码" prop="supplierCode" align="center" width="180"></el-table-column>
            <el-table-column label="商家名称" prop="supplierName" align="center" width="220" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column label="物流方式" prop="logisticsType" align="center" width="220" show-overflow-tooltip :formatter="format_status"></el-table-column> -->
            <el-table-column label="SPU ID" prop="goodsId" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="一级类目" prop="oneCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="二级类目" prop="twoCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="三级类目" prop="threeCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="四级类目" prop="fourCatName" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="商品标题" prop="goodsName" align="center" width="300" show-overflow-tooltip></el-table-column>
            <el-table-column label="SPU编码" prop="goodsSn" align="center" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="品牌名称" prop="brandName" align="center" width="180"></el-table-column>
            <el-table-column label="否存在库存" prop="isQty" align="center" width="120">
                <template slot-scope="scope">{{scope.row.isQty==1?"有":"无"}}</template>
            </el-table-column>
            <el-table-column label="是否上下架" prop="isOnSale" align="center" width="120">
                <template slot-scope="scope">{{scope.row.isOnSale==1?"上架":"下架"}}</template>
            </el-table-column>
            <el-table-column label="原价" prop="marketPrice" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.marketPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="售价" prop="shopPrice" align="right" width="100">
                <template slot-scope="scope">{{(scope.row.shopPrice/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="审核状态" prop="auditStatus" align="center" width="180" :formatter="format_status">
        
            </el-table-column>
            <el-table-column label="SPU库存" prop="stock" align="center" width="100"></el-table-column>
            <el-table-column label="sku数量" prop="skuNum" align="center" width="100"></el-table-column>
            <el-table-column label="上/下架时间" prop="saleTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.saleTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="spu添加时间" prop="addTime" align="center" width="160">
                <template slot-scope="scope">{{scope.row.addTime|dateFormat }}</template>
            </el-table-column>

        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>

<script>
import format from "date-format";
export default {
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        supplierCode: "",
        goodsSn: "",
        auditStatus: "",
        brandId: "",
        brandName: "",
        isQty: "",
        isOnSale: "",
        oneCatId: "",
        twoCatId: "",
        threeCatId: "",
        fourCatId: "",
        addTimeStart:'',
        addTimeEnd:'',
        onSaleTimeStart:'',
        onSaleTimeEnd:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },

      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      // 合作商列表
      cpaList: [],
      //商品分类列表
      cateList: [],
      props: {
        value: "catId",
        label: "catName"
      }
    };
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    }
  },
  methods: {
    //列表状态过滤
    format_status(row, column, cellValue) {
      for (let i in this.myConfig.status[column.property]) {
        if (
          this.myConfig.status[column.property][i].val == row[column.property]
        ) {
          return this.myConfig.status[column.property][i].label;
          break;
        }
      }
      return row[column.property];
    },
    DateToString() {
      for (let i in this.searchForm) {
          //日期转换
          if (this.searchForm[i] instanceof Date) {        
                this.searchForm[i] = format('yyyy-MM-dd', new Date(this.searchForm[i]));  
          }
          //去掉前后空格
          if (this.searchForm[i] instanceof String) {
              this.searchForm[i] = this.searchForm[i].trim();
          }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //报表列表获取
    getReportList() {
      this.loading = true;
      this.axios
        .post(this.myConfig.code.proxy + "/report/api/report/operation/goods", {
          body: this.searchForm
        })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    //获取cpa列表
    getCpaList() {
      this.axios
        .post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} })
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cpaList = data.body.list;
          }
        });
    },
    //获取商品分类列表
    getCateList() {
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/common/category/options",
          { body: {} }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.cateList = data.body;
          }
        });
    },
    export_smt() {
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj)
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" + encodeURIComponent(obj[p]));
          }
        return str.join("&");
      }
      
         window.open("/proxy/report/api/export/operation/goods?" + serialize(tmp));
    },

    //选择分类
    cateChange(change) {
      this.searchForm.oneCatId = "";
      this.searchForm.twoCatId = "";
      this.searchForm.threeCatId = "";
      this.searchForm.fourCatId = "";
      for (let [index, item] of change.entries()) {
        switch (index) {
          case 0:
            this.searchForm.oneCatId = item;
            break;
          case 1:
            this.searchForm.twoCatId = item;
            break;
          case 2:
            this.searchForm.threeCatId = item;
            break;
          case 3:
            this.searchForm.fourCatId = item;
            break;
        }
      }
    }
  },
  created() {
    this.getCpaList();
    this.getCateList();
    this.getReportList();
  }
};
</script>




