<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>运营统计报表</el-breadcrumb-item>
                <el-breadcrumb-item>品牌销售统计</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                        <el-form-item label="本期时间">
                            <el-date-picker v-model="searchForm.time" type="daterange" placeholder="本期时间"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="对比期时间">
                            <el-date-picker v-model="searchForm.compareTime" type="daterange" placeholder="本期时间"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="商家编号">
                            <el-input v-model="searchForm.supplierCode" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="SPU编号">
                            <el-input v-model="searchForm.goodsSn" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="品牌ID">
                            <el-input v-model="searchForm.brandId" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="品牌名称">
                            <el-input v-model="searchForm.brandName" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="流量来源">
                            <el-select v-model="searchForm.orderSource" placeholder="流量来源" size="small">
                                <el-option value="" label="所有"></el-option>
                                <el-option :value="item.code" :label="item.name" v-for="(item,key) in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="支付方式">
                            <el-select v-model="searchForm.payMethod" size="small">
                                <el-option value="" label="所有"></el-option>
                                <el-option :value="item.val" :label="item.label" v-for="(item,index) in myConfig.status.payMethod" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="国家">
                            <el-input v-model="searchForm.country" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="城市">
                            <el-input v-model="searchForm.province" size="small"></el-input>
                        </el-form-item>

                        <el-form-item label="分类">
                            <el-cascader placeholder="分类" :options="cateList" change-on-select @change="cateChange" :props="props" filterable :show-all-levels="false" clearable></el-cascader>
                        </el-form-item>
                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="primary" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>

                        </el-form-item>

                    </div>
                </transition>

            </el-form>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe border style="width: 100%" v-loading="loading" class="report" max-height="520" highlight-current-row border>
            <el-table-column type="index" width="70" :fixed="'left'"></el-table-column>
            <el-table-column label="公共信息">
                <el-table-column prop="info.brandName" label="品牌" width="180" show-overflow-tooltip></el-table-column>
            </el-table-column>
            <el-table-column label="本期">
                <el-table-column prop="current.amountPercent" label="销售金额占比%" width="150" align="right"></el-table-column>
                <el-table-column prop="current.quantityPercent" label="销售数量占比%" width="150" align="right"></el-table-column>
                <el-table-column label="销售金额" width="100" align="right">
                    <template slot-scope="scope">{{(scope.row.current.salesAmount/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column prop="current.salesQuantity" label="销售数量" width="100" align="right"></el-table-column>
                <el-table-column label="销售单价" width="100" align="right">
                    <template slot-scope="scope">{{(scope.row.current.unitPrice/100).toFixed(2)}}</template>
                </el-table-column>
            </el-table-column>
            <el-table-column label="对比期">
                <el-table-column label="销售金额" width="100" align="right">
                    <template slot-scope="scope">{{(scope.row.compare.salesAmount/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column prop="compare.salesQuantity" label="销售数量" width="100" align="right"></el-table-column>
                <el-table-column label="销售单价" width="100" align="right">
                    <template slot-scope="scope">{{(scope.row.compare.unitPrice/100).toFixed(2)}}</template>
                </el-table-column>
                <el-table-column prop="compare.amountPercent" label="销售金额占比%" width="150" align="right"></el-table-column>
                <el-table-column prop="compare.quantityPercent" label="销售数量占比%" width="150" align="right"></el-table-column>
            </el-table-column>
            <el-table-column label="增长率">
                <el-table-column prop="incrRate.amountPercent" label="销售金额占比%" width="150" align="right"></el-table-column>
                <el-table-column prop="incrRate.quantityPercent" label="销售数量占比%" width="150" align="right"></el-table-column>
                <el-table-column prop="incrRate.salesAmount" label="销售金额%" width="120" align="right"></el-table-column>
                <el-table-column prop="incrRate.salesQuantity" label="销售数量%" width="120" align="right"></el-table-column>
                <el-table-column prop="incrRate.unitPrice" label="销售单价%" width="120" align="right"></el-table-column>
            </el-table-column>

        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            //搜索折叠
            fold: true,
            searchForm: {
                time: [],
                compareTime: [],
                supplierCode: '',
                goodsSn: '',
                brandId: '',
                brandName: '',
                orderSource: '',
                payMethod: '',
                country: '',
                province: '',
                oneCatId: '',
                twoCatId: '',
                threeCatId: '',
                fourCatId: '',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }

                }
            },
            //报表列表
            loading: false,
            total: 0,
            reportList: [],
            // 合作商列表
            cpaList: [],
            //商品分类列表
            cateList: [],
            props: {
                value: 'catId',
                label: 'catName'
            }

        }
    },
    filters: {

        dateFormat(date) {
            return date ? format('yyyy-MM-dd hh:mm', new Date(date)) : '';
        }
    },
    methods: {
        checkParms() {
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
                if (this.searchForm[i] instanceof Array) {
                    for (let k in this.searchForm[i]) {
                        if (this.searchForm[i][k] instanceof Date) {
                            this.searchForm[i][k]=format('yyyy-MM-dd', new Date(this.searchForm[i][k]))
                        }
                    }
                }
            }
        },
        search_smt() {
            this.searchForm.page.page = 1;
            //去掉前后空格
            this.checkParms();
            this.getReportList();
        },
        //报表列表获取
        getReportList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/report/api/report/statis/brand', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getReportList();
        },
        //获取cpa列表
        getCpaList() {
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: {} }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })

        },
        //获取商品分类列表
        getCateList() {
            this.axios.post(this.myConfig.code.proxy + '/report/api/common/category/options', { body: {} }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cateList = data.body;
                }
            })
        },
        export_smt() {
            this.checkParms();
            var tmp = JSON.parse(JSON.stringify(this.searchForm));
            delete tmp.page;
            function serialize(obj) {
                var str = [];
                for (var p in obj) {
                    if (obj.hasOwnProperty(p) && obj[p] != "") {
                        if (p == 'time') {
                            str.push("startTime=" + obj[p][0].substring(0, 10));
                            str.push("endTime=" + obj[p][1].substring(0, 10));
                        } else if (p == 'compareTime') {
                            str.push("compareStartTime=" + obj[p][0].substring(0, 10));
                            str.push("compareEndTime=" + obj[p][1].substring(0, 10));
                        } else {
                            str.push(p + "=" + obj[p]);
                        }
                    }
                }
                return str.join("&");
            }
             window.open('/proxy/report/api/export/statis/brand?' + serialize(tmp));
        },

        //选择分类
        cateChange(change) {
            this.searchForm.oneCatId = '';
            this.searchForm.twoCatId = '';
            this.searchForm.threeCatId = '';
            this.searchForm.fourCatId = '';
            for (let [index, item] of change.entries()) {
                switch (index) {
                    case 0:
                        this.searchForm.oneCatId = item;
                        break;
                    case 1:
                        this.searchForm.twoCatId = item;
                        break;
                    case 2:
                        this.searchForm.threeCatId = item;
                        break;
                    case 3:
                        this.searchForm.fourCatId = item;
                        break;
                }
            }

        }

    },
    created() {
        this.getCpaList();
        this.getCateList();
        this.getReportList();
    }
}
</script>




