<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>财务结算</el-breadcrumb-item>
                <el-breadcrumb-item>查询汇率列表</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增-->
            <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i>新增汇率</el-button>
            </div>
            <!--新增表单-->
            <el-dialog :title="addForm.exchangeId==''?'新增汇率':'修改汇率'" :visible.sync="dialogAddForm" class="add-form" :close-on-click-modal="false" size="small">
                <el-form :model="addForm" label-width="150px" ref="addForm">
                    <el-form-item label="原始币种">
                        <el-select v-model="addForm.originalCurrency" size="small">
                            <el-option value="USD" label="美元"></el-option>
                            <el-option value="CNY" label="人民币"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="目标币种">
                        <el-select v-model="addForm.targetCurrency" size="small">
                            <el-option value="USD" label="美元"></el-option>
                            <el-option value="CNY" label="人民币"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="状态">
                        <el-select v-model="addForm.status" size="small">
                            <el-option value="0" label="无效"></el-option>
                            <el-option value="1" label="有效"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="汇率" prop="exchangeRate" :rules="{required:true,message:'该字段必填'}">
                        <el-input v-model="addForm.exchangeRate"></el-input>
                    </el-form-item>
                    <el-form-item label="汇率月份" prop="effectiveTime" :rules="{required:true,message:'该字段必填'}">
                        <el-date-picker placeholder="选择日期" v-model="addForm.effectiveTime" type="month"></el-date-picker>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false">取 消</el-button>
                    <el-button type="primary" @click="submit_add">确 定</el-button>
                </div>
            </el-dialog>
        </div>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <!-- <i class="el-icon-search"></i> -->
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">
                      
                        <el-form-item label="原始币种">
                            <el-select v-model="searchForm.originalCurrency" size="small">
                                <el-option value="USD" label="美元"></el-option>
                                <el-option value="CNY" label="人民币"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="目标币种">
                            <el-select v-model="searchForm.targetCurrency"  size="small">
                                <el-option value="USD" label="美元"></el-option>
                                <el-option value="CNY" label="人民币"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="结算状态">
                            <el-select v-model="searchForm.status"  size="small">
                                <el-option value="0" label="无效"></el-option>
                                <el-option value="1" label="有效"></el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item class="search-btn">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                        </el-form-item>
                    </div>
                </transition>

            </el-form>
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="ID" prop="exchangeId" align="center" width="180"></el-table-column>
            <el-table-column label="原始币种" prop="originalCurrency" align="center" width="160"></el-table-column>
            <el-table-column label="目标币种" prop="targetCurrency" align="center" width="160"></el-table-column>
            <el-table-column label="状态" prop="status" align="center" width="100">
                <template slot-scope="scope">{{scope.row.status==0?'无效':'有效'}}</template>
            </el-table-column>
            <el-table-column label="结算日汇率" prop="exchangeRate" align="right" width="160">
                 <template slot-scope="scope">{{scope.row.exchangeRate.toFixed(4) }}</template>
            </el-table-column>
            <el-table-column label="汇率结算时间" prop="effectiveTime" align="center" width="">
            </el-table-column>
            <el-table-column label="创建时间" prop="buildTime" align="center" width="">
                <template slot-scope="scope">{{scope.row.buildTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="更新用户" prop="optUser" align="center" width="120"> </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <i title="修改" class="primary iconfont icon-update" @click="addForm_btn(scope.row)"></i>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<style lang="less" scoped>
.wrap {
  padding-right: 40px;
}
</style>
<style lang="less">
.add-form {
  .el-dialog__body {
    height: 400px;
    overflow-y: auto;
  }
  .el-dialog--small {
    width: 40%;
  }
  .el-dialog {
    margint-bottom: 30px;
  }
  .el-input__inner {
    width: 300px;
  }
  .el-date-editor.el-input {
    width: 300px;
  }
}
</style>


<script>
import format from "date-format";
export default {
  data() {
    return {
      //搜索折叠
      fold: true,
      searchForm: {
        originalCurrency: "",
        status: "",
        targetCurrency: "",
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },
      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      //新增菜单
      dialogAddForm: false,
      addForm: {
        originalCurrency: "",
        status: "",
        targetCurrency: "",
        exchangeRate: "",
        effectiveTime: "",
        exchangeId: ""
      }
    };
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    }
  },
  methods: {
    DateToString() {
      for (let i in this.searchForm) {
        //日期转换
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format(
            "yyyy-MM-dd",
            new Date(this.searchForm[i])
          );
        }
        //去掉前后空格
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getReportList();
    },
    //报表列表获取
    getReportList() {
      this.loading = true;
      this.axios
        .post(
          this.myConfig.code.proxy + "/report/api/report/financial/exchange",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading = false;
            this.reportList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getReportList();
    },
    doUpdate(data) {},
    //增加按钮
    addForm_btn(row) {
      this.dialogAddForm = true;
      if (row != "") {
        //修改
        this.addForm = {
          originalCurrency: row.originalCurrency,
          status: row.status.toString(),
          targetCurrency: row.targetCurrency,
          exchangeRate: row.exchangeRate,
          effectiveTime: row.effectiveTime,
          exchangeId: row.exchangeId
        };
      } else {
        //添加
        this.addForm = {
          originalCurrency: "",
          status: "",
          targetCurrency: "",
          exchangeRate: "",
          effectiveTime: "",
          exchangeId: ""
        };
      }
    },
    //添加表单提交
    submit_add() {
      for (let i in this.addForm) {
        //日期转换
        if (this.addForm[i] instanceof Date) {
          this.addForm[i] = format("yyyy-MM", new Date(this.addForm[i]));
        }
        //去掉前后空格
        if (this.addForm[i] instanceof String) {
          this.addForm[i] = this.addForm[i].trim();
        }
      }
      this.$refs.addForm.validate(validate => {
        if (validate) {
          this.axios
            .post(
              this.myConfig.code.proxy + "/report/api/financial/save/exchange",
              { body: this.addForm }
            )
            .then(response => {
              let data = response.data;
              if (data.head.code == "10000") {
                this.$message({
                  type: "success",
                  message: "操作成功",
                  duration: 1000
                });
                this.dialogAddForm = false;
                this.getReportList();
              }
            });
        }
      });
    },
    //数据导出
    export_smt() {
      this.DateToString();
      var tmp = JSON.parse(JSON.stringify(this.searchForm));
      delete tmp.page;
      function serialize(obj) {
        var str = [];
        for (var p in obj) {
          if (obj.hasOwnProperty(p) && obj[p] != "") {
            str.push(p + "=" + obj[p]);
          }
        }
        return str.join("&");
      }
      
         window.open("/proxy/report/api/export/financial/cost?" + serialize(tmp));
    }
  },
  created() {
    this.getReportList();
  }
};
</script>




